/* DorpDownLisk.css */

/* �����˦� */
.body-container {
    font-family: Arial, sans-serif;
    background-color: #F8F8F8;
    margin: 0;
    padding: 20px;
    text-align: center;
}

/* ���e�� */
.dropdown-container {
    display: inline-block;
    margin: 10px 0;
}

/* �U�Ԧ���� */
.dropdown-select {
    width: 250px;
    padding: 10px;
    border: 2px solid #586D80;
    border-radius: 8px;
    background: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
    color: #333;
    font-size: 16px;
    transition: all 0.3s ease;
    outline: none;
    cursor: pointer;
    margin: 20px;
}

    /* Hover �ĪG */
    .dropdown-select:hover {
        border-color: #455A64;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    }

    /* Focus �ĪG */
    .dropdown-select:focus {
        border-color: #1E88E5;
        box-shadow: 0 4px 10px rgba(30, 136, 229, 0.4);
        background: #f0f8ff;
    }

    /* �T�Ϊ��A */
    .dropdown-select:disabled {
        background: #ddd;
        color: #999;
        border-color: #ccc;
        box-shadow: none;
        cursor: not-allowed;
    }

    /* ���~���A */
    .dropdown-select:focus:invalid {
        border-color: #e57373; /* �L�Ī��A */
        animation: shake 0.2s ease-in-out;
    }

/* Shake �ʵe */
@keyframes shake {
    0%, 100% {
        transform: translateX(0);
    }

    25%, 75% {
        transform: translateX(-5px);
    }

    50% {
        transform: translateX(5px);
    }
}

/* ���Ҥ�r */
.dropdown-label {
    font-size: 18px;
    color: #586D80;
    margin: 10px 0;
}

/* ��ܿ�ܵ��G����r�˦� */
.dropdown-result {
    font-weight: bold;
    color: #37474F;
    margin-top: 15px;
}
